export default {

	data() {
		return {
			mainData: [],
			Router:this.$Router,
			choosedIndex: -1
		}
	},

	onLoad(options) {
		const self = this;
		var res = self.$Token.getProjectToken(function() {
			self.$Utils.loadAll(['getMainData'], self)
		});
		if (res) {
			self.$Utils.loadAll(['getMainData'], self)
		};
	},

	onReachBottom() {
		console.log('onReachBottom')
		const self = this;
		if (!self.isLoadAll && uni.getStorageSync('loadAllArray')) {
			self.paginate.currentPage++;
			self.getMainData()
		};
	},

	methods: {

		choose(index) {
			const self = this;
			self.choosedIndex = index;
			uni.setStorageSync('choosedAddressData', self.mainData[index]);
			console.log('choosedIndex', self.choosedIndex);
		},

		getMainData() {
			const self = this;

			const postData = {};
			postData.paginate = self.$Utils.cloneForm(self.$AssetsConfig.paginate);
			postData.tokenFuncName = 'getProjectToken';
			const callback = (res) => {
				if (res.info.data.length > 0) {
					self.mainData.push.apply(self.mainData, res.info.data);
				} else {
					self.$Utils.showToast('没有更多了', 'none');
				};
				self.$Utils.finishFunc('getMainData');
			};
			self.$apis.addressGet(postData, callback);
		},





		deleteAddress(id) {
			const self = this;
			const postData = {};
			postData.searchItem = {};
			postData.searchItem.id = id;
			postData.tokenFuncName = 'getProjectToken';
			const callback = (res) => {
				if (res) {
					self.mainData = [];
					self.getMainData();
				}
			};
			self.$apis.addressDelete(postData, callback)
		},


		updateAddress(id) {
			const self = this;
			const postData = {};

			postData.tokenFuncName = 'getProjectToken';

			postData.searchItem = {};
			postData.searchItem.id = id;
			postData.data = {
				isdefault: 1
			}
			const callback = (res) => {
				if (res) {
					self.mainData = [];
					self.getMainData();
				}
			};
			self.$apis.addressUpdate(postData, callback);
		},




	}
}
